.main-footer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  background-color: $second-background-color;

  @media (min-width: $mobile-above) {
    flex-direction: row;
    justify-content: space-between;
    padding: 55px 30px;
  }

  @media (min-width: $desktop-above) {
    padding: 66px 110px;
  }
}

.main-footer__logo-text {
  @media (min-width: $mobile-above) {
    margin-right: 52px;
  }

  &:hover,
  &:focus {
    fill: #3e3e3e;
  }

  &:active {
    fill: #6b6b6b;
  }
}

.main-footer__social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 280px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  border-top: 1px solid $border-color;
  border-bottom: 1px solid $border-color;

  @media (min-width: $mobile-above) {
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
    padding-bottom: 0;
    border: none;
  }
}

.main-footer__academy-link {
  margin-left: auto;

  @media (min-width: $mobile-xs-above) {
    width: 180px;
    justify-content: space-between;
    margin-left: initial;
  }

  @media (min-width: $mobile-above) {
    justify-content: flex-end;
    column-gap: 22px;
  }
}
